"use client"

import { useRef } from "react"
import { ChevronLeft, ChevronRight } from "lucide-react"
import Image from "next/image"

export interface CategoryHighlight {
  name: string
  image: string
  count: number
}

interface CategoryCarouselProps {
  categories: CategoryHighlight[]
}

export default function CategoryCarousel({ categories }: CategoryCarouselProps) {
  const scrollRef = useRef<HTMLDivElement>(null)

  const scroll = (direction: "left" | "right") => {
    if (!scrollRef.current) return
    scrollRef.current.scrollBy({
      left: direction === "left" ? -320 : 320,
      behavior: "smooth",
    })
  }

  return (
    <section className="py-12 bg-secondary">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex items-center justify-between mb-8">
          <div className="flex gap-4">
            <button onClick={() => scroll("left")} className="p-2 hover:bg-background rounded-full transition" aria-label="Scroll categories left">
              <ChevronLeft className="w-5 h-5" />
            </button>
            <button
              onClick={() => scroll("right")}
              className="p-2 hover:bg-background rounded-full transition"
              aria-label="Scroll categories right"
            >
              <ChevronRight className="w-5 h-5" />
            </button>
          </div>
        </div>

        <div ref={scrollRef} className="flex gap-6 overflow-x-auto scroll-smooth pb-4">
          {categories.map((category) => (
            <button
              key={category.name}
              className="flex-shrink-0 text-center group focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-primary rounded-lg px-2"
            >
              <div className="relative w-24 h-24 rounded-full overflow-hidden bg-muted mb-3 mx-auto border-2 border-background group-hover:border-primary transition">
                <Image src={category.image || "/placeholder.jpg"} alt={category.name} fill className="object-cover" sizes="96px" />
              </div>
              <p className="text-xs font-semibold text-foreground uppercase tracking-wide">{category.name}</p>
              <p className="text-[11px] text-muted-foreground">{category.count} recipes</p>
            </button>
          ))}
        </div>
      </div>
    </section>
  )
}
